import { Struct } from 'rcfm'
import { UI } from 'rcfm/helper'
import React from 'react'



export default function ValidationItemRule(props = {}) {
  const Helper = UI.Section.Form.Member.PropEditor.Validation
  let { reactive, member, value, onChange } = props
  let RuleType = Helper.getRuleType({ reactive, member, value })
  if (!RuleType) { return null }
  let NameCap = Struct.string.capitalize(value.name)
  const RuleEditor = UI.try([
    `React.Section.Form.Member.PropEditor.Validation.ItemRule.${NameCap}`,
    `React.Section.DataType.Editor.${RuleType}`,
  ])
  return (
    <RuleEditor
      reactive={reactive} member={member} value={value.rule}
      onChange={(rule) => onChange({ action: 'update', rule })}
    />
  )
}
