import { UI } from 'rcfm/helper'
import React from 'react'



export default function Alert(props = {}) {
  const GetProps = UI.Component.Alert.getProps
  const UseTimer = UI.React.hooks.useTimer
  let alertProps = GetProps(props)
  let timer = UseTimer(alertProps.dismiss)
  delete alertProps.dismiss
  if (!timer) { return null }
  return (
    <div {...alertProps} >{alertProps.children}</div>
  )
}
