import { UI } from 'rcfm/helper'
import React from 'react'



export default function Fieldset(props = {}) {
  const GetProps = UI.Section.Form.Member.getProps
  const UseReactive = UI.React.hooks.useReactive
  const Header = UI.use('React.Section.Form.Fieldset.Header')
  const FormMember = UI.use('React.Section.Form.Member')
  let { reactive, member, onAction } = GetProps(props)
  let state = UseReactive(reactive, { id: member.id })
  return (<div className="rcfm-form-fieldset">
    <Header reactive={reactive} member={member} />
    {state.values.children.map((chd) =>
      <FormMember
        key={chd.id}
        reactive={reactive}
        member={chd}
        onAction={onAction}
      />,
    )}
  </div>)
}
