import { UI } from 'rcfm/helper'
import React from 'react'



export default function Checkboxes(props = {}) {
  const GetProps = UI.Component.Form.Checkboxes.getProps
  const GetNewValue = UI.Component.Form.Checkboxes.getNewValue
  const UseNonblankName = UI.React.hooks.useNonblankName
  let { inputProps, wrapperProps, value, options } = GetProps(props)
  inputProps.readonly && (inputProps.disabled = true)
  inputProps.readOnly = inputProps.readonly
  delete inputProps.readonly
  let name = UseNonblankName(inputProps.name)
  let onChange = inputProps.onChange
  return (<div {...wrapperProps} >
    {options.map((o, i) =>
      <label className="rcfm-fmtag" key={i}>
        <input {...{
          ...inputProps, name, value: o.value,
          [!onChange ? 'defaultChecked' : 'checked']: value.includes(o.value),
          onChange: !onChange ? null : (e) => {
            onChange(GetNewValue(e, value, o.value))
          },
        } } />
        {o.label}
      </label>,
    )}
  </div>)
}
