import { UI } from 'rcfm/helper'
import React from 'react'



export default function TextInput(props = {}) {
  const GetProps = UI.Component.Form.TextInput.getProps
  const UseNonblankName = UI.React.hooks.useNonblankName
  const UseDebounceOnChange = UI.React.hooks.useDebounceOnChange
  let { multiline, inputProps, wrapperProps } = GetProps(props)
  inputProps.readonly && (inputProps.disabled = true)
  inputProps.readOnly = inputProps.readonly
  delete inputProps.readonly
  let name = UseNonblankName(inputProps.name)
  let { value, onChange } = UseDebounceOnChange({
    debounce: inputProps.debounce, value: inputProps.value,
    onChange: inputProps.onChange, parseValue: (e) => e.target.value,
  })
  Object.assign(inputProps, { name, value, onChange })
  return (<div {...wrapperProps} >
    {
      multiline ?
        <textarea {...inputProps} ></textarea> :
        <input {...inputProps} />
    }
  </div>)
}
