import { UI } from 'rcfm/helper'
import React from 'react'



export default function Tabs(props = {}) {
  const GetProps = UI.Component.Tabs.getProps
  const ActivateClass = UI.Component.Tabs.activateClass
  let {
    wrapperProps, title, labels,
    activeIndex, onSwitch, classNames,
  } = GetProps(props)
  return (<div {...wrapperProps} >
    <div className={classNames.header}>
      {title && <h6 className={classNames.title}>{title}</h6>}
      {labels.map((label, index) =>
        <label key={label}
          className={ActivateClass(classNames.label, index, activeIndex)}
          onClick={() => onSwitch && onSwitch(index) } >
          {label}
        </label>,
      )}
    </div>
    {!labels.length ?
      <div
        className={ActivateClass(classNames.pane, -1, -1)}>
        {wrapperProps.children}
      </div> :
      labels.map((label, index) =>
        <div key={label}
          className={ActivateClass(classNames.pane, index, activeIndex)}>
          {wrapperProps.children[index]}
        </div>,
      )}
  </div>)
}
