import { UI } from 'rcfm/helper'
import React, { useRef, useState } from 'react'



export default function ChildPicker(props = {}) {
  const UseOnClickOutside = UI.React.hooks.useOnClickOutside
  const [Button, ChildPannel] = [
    UI.use('React.Component.Button'),
    UI.use('React.Section.Form.Fieldset.Previewer.ChildPicker.Pannel'),
  ]
  let ref = useRef()
  let [pannelVisible, setPannelVisible] = useState(false)
  UseOnClickOutside(ref, () => setPannelVisible(false))
  return (
    <div className="rcfm-form-fieldset-previewer-child-picker">
      <Button
        className="rcfm-form-fieldset-previewer-child-picker-trigger"
        circle color="dark" icon="plus" onClick={() => setPannelVisible(true)}
      />
      {!pannelVisible ? null : <div
        className="rcfm-form-fieldset-previewer-child-picker-pannel-wrapper">
        <div ref={ref}><ChildPannel onPick={(type) => {
          setPannelVisible(false)
          props.onPick && props.onPick(type)
        }} /></div>
      </div>}
    </div>
  )
}
