import { useState, useEffect } from 'react'



function useReactive(reactive, member = {}) {
  member || (member = {})
  let { id, prop } = member
  let [value, setValue] = useState(getProp(reactive, member))
  useEffect(() => {
    if (!id) { return }
    return reactive.subscribe({ id }, () => {
      setValue(getProp(reactive, prop ? { id, prop } : { id }))
    })
  }, [reactive, id, prop])
  return value
}

function getProp(reactive, member){
  return reactive.get(member, { include: ['raw', 'error'] })
}



export default useReactive
