import { I18n } from 'rcfm'
import { UI } from 'rcfm'
import React from 'react'



export default function ElseResult(props = {}) {
  const Helper = UI.Section.Form.Member.PropEditor.Condition
  let { reactive, member, value, onChange, ResultComponent, exception } = props
  return (<div className="rcfm-form-member-editor-condition-else-branch">
    <h6 className="rcfm-form-member-editor-condition-branch-title">
      {I18n.global.t(
        'section.form.member.propEditor.condition.elseBranch.resultTitle',
      )}
    </h6>
    <ResultComponent
      reactive={reactive} member={member} value={value.result}
      exception={Helper.isntConditionException(exception) ? exception : null}
      onChange={(result) => onChange({
        target: 'else', action: 'update', result,
      })}
    />
  </div>)
}
