import { UI } from 'rcfm/helper'
import React from 'react'



export default function Highlight(props = {}) {
  const GetProps = UI.Component.Highlight.getProps
  const Alert = UI.use('React.Component.Alert')
  const AlertText = UI.use('React.Component.AlertText')
  let { on, wrapperProps, hintProps } = GetProps(props)
  return (
    <div {...wrapperProps}>
      {wrapperProps.children}
      {!on || !hintProps.hint ? null :
        <Alert className={hintProps.className} type={hintProps.type}>
          <AlertText>{hintProps.hint}</AlertText>
        </Alert>
      }
    </div>
  )
}
