import { useState, useEffect } from 'react'



function useTimer(duration) {
  let [flag, setFlag] = useState(false)
  useEffect(() => {
    setFlag(true)
    let timer = null
    if (duration !== Infinity) {
      timer = setTimeout(() => setFlag(false), duration)
    }
    return () => {
      clearTimeout(timer)
      setFlag(false)
    }
  }, [duration])
  return flag
}



export default useTimer
