import { UI } from 'rcfm/helper'
import React from 'react'



export default function OptionsEditor(props = {}) {
  const Helper = UI.Section.Form.Member.PropEditor.Options
  const [Highlight, Option] = [
    UI.use('React.Component.Highlight'),
    UI.use('React.Section.Form.Member.PropEditor.Options.Item'),
  ]
  let { reactive, member, exception, value, onChange } = Helper.getProps(props)
  return (<div className="rcfm-form-member-editor-prop-options">
    <Highlight type="error"
      hint={exception?.message}
      on={exception && !('optionIndex' in exception.data) ? true : false}
    >
      {value.map((option, index) =>
        <Option
          key={index}
          reactive={reactive} member={member} value={option}
          onChange={(mu) => onChange({ ...mu, index })}
          exception={exception?.data?.optionIndex === index ? exception : null}
        />,
      )}
    </Highlight>
  </div>)
}
