import { UI } from 'rcfm/helper'
import React, { useRef, useState, useEffect } from 'react'



export default function Builder(props = {}) {
  const UseReactive = UI.React.hooks.useReactive
  const UseOnClickOutside = UI.React.hooks.useOnClickOutside
  const Alert = UI.use('React.Component.Alert')
  const AlertText = UI.use('React.Component.AlertText')
  const MemberPreviewer = UI.use('React.Section.Form.Member.Previewer')
  const MemberEditor = UI.use('React.Section.Form.Member.Editor')
  let reactive = UI.getProp(props, 'reactive', { required: true })
  let [id, editRef] = [reactive.family.root().id, useRef()]
  let children = UseReactive(reactive, { id, prop: 'children' }).value
  if (!children.length) { reactive.add({ props: { type: 'Fieldset' } }) }
  let [editId, setEditId] = useState('')
  UseOnClickOutside(editRef, () => setEditId(''))
  let [exception, setException] = useState(null)
  let [exceptionTimer, setExceptionTimer] = useState(null)
  useEffect(() => () => clearTimeout(exceptionTimer))
  let onAction = (action = {}) => {
    if (action.action === 'add') {
      reactive.add({ parentId: action.parentId, props: action.props })
    } else if (action.action === 'delete') {
      try {
        reactive.delete({ id: action.id })
        setEditId('')
      } catch (error) {
        setException(error)
        setExceptionTimer(setTimeout(() => setException(null), 3000))
      }
    } else if (action.action === 'update') {
      reactive.set({ id: action.id, prop: action.prop, value: action.value })
    } else if (action.action === 'edit') {
      setEditId(action.id)
    }
  }
  return (<div className={UI.joinClass([
    'rcfm-view-form-builder',
    editId ? 'rcfm-view-form-builder-editor-active' : '',
  ])} >
    <div className="rcfm-view-form-builder-previewer">
      <div className="rcfm-view-form-builder-previewer-content">
        {children.map((chd) =>
          <MemberPreviewer
            key={chd.id}
            reactive={reactive} member={chd}
            onAction={onAction} highlight={editId}
          />,
        )}
      </div>
    </div>
    {editId && <div className="rcfm-view-form-builder-editor">
      <div ref={editRef} className="rcfm-view-form-builder-editor-content">
        <MemberEditor
          reactive={reactive} member={{ id: editId }} onAction={onAction}
        />
      </div>
    </div>}
    {exception && <Alert type="error">
      <AlertText>{exception.message}</AlertText>
    </Alert>}
  </div>)
}
