import { UI } from 'rcfm/helper'
import React from 'react'



export default function PreviewerActions(props = {}) {
  const Button = UI.use('React.Component.Button')
  const UseReactive = UI.React.hooks.useReactive
  const Helper = UI.Section.Form.Member.Previewer.Actions
  let { reactive, member } = UI.Section.Form.Member.getProps(props)
  let { actions, onAction } = Helper.getProps(props)
  let id = member.id
  let order = UseReactive(reactive, { id, prop: 'order' }).value
  let maxOrder = reactive.family.sibling({ id }, { self: true }).length - 1
  return (<div className="rcfm-form-member-previewer-actions">
    {actions && actions.includes('delete') ? <Button
      className="rcfm-form-member-previewer-delete"
      circle icon="cross" color="danger" size="small"
      onClick={() => onAction({
        action: 'delete', reactive, id,
      })}
    /> : null}
    {actions && actions.includes('decrease') ? <Button
      className="rcfm-form-member-previewer-decrease"
      circle icon="up" color="secondary" size="small"
      disabled={order === null || order === 0}
      onClick={() => onAction({
        action: 'update', reactive,
        id, prop: 'order', value: order - 1,
      })}
    /> : null}
    {actions && actions.includes('increase') ? <Button
      className="rcfm-form-member-previewer-increase"
      circle icon="down" color="secondary" size="small"
      disabled={order === null || order === maxOrder}
      onClick={() => onAction({
        action: 'update', reactive,
        id, prop: 'order', value: order + 1,
      })}
    /> : null}
    {actions && actions.includes('edit') ? <Button
      className="rcfm-form-member-previewer-edit"
      circle icon="menu" color="secondary" size="small"
      onClick={() => onAction({
        action: 'edit', reactive, id,
      })}
    /> : null}
    {actions && actions.includes('add') ? <Button
      className="rcfm-form-member-previewer-add"
      circle icon="plus" color="dark" size="small"
      onClick={() => onAction({
        action: 'add', reactive,
        parentId: reactive.family.ancestor(member).id,
        props: { type: 'default', order: order + 1 },
      })}
    /> : null}
  </div>)
}
