import { UI } from 'rcfm/helper'
import React from 'react'



export default function Renderer(props = {}) {
  const UseReactive = UI.React.hooks.useReactive
  const MemberRenderer = UI.use('React.Section.Form.Member')
  let reactive = UI.getProp(props, 'reactive', { required: true })
  let { id } = reactive.family.root()
  let children = UseReactive(reactive, { id, prop: 'children' }).value
  return (<div className="rcfm-view-form">
    {children.map((chd) =>
      <MemberRenderer
        key={chd.id}
        reactive={reactive} member={chd}
        onAction={(action) => reactive.set({
          id: action.id, prop: action.prop, value: action.value,
        })}
      />,
    )}
  </div>)
}
