import { UI } from 'rcfm/helper'
import React from 'react'



export default function Field(props = {}) {
  const GetProps = UI.Component.Form.Field.getProps
  let { wrapperProps, labelProps, hintProps, errorProps } = GetProps(props)
  return (<div {...wrapperProps} >
    {labelProps.label &&
    <label className={labelProps.className} >
      {labelProps.label}
    </label>}
    {props.children}
    {errorProps.error &&
    <p className={errorProps.className} >
      {errorProps.error.map((e) => e + ' ')}
    </p>}
    {hintProps.hint && !errorProps.error &&
    <p className={hintProps.className} >
      {hintProps.hint}
    </p>}
  </div>)
}
