import  { Struct } from 'rcfm'
import { UI } from 'rcfm/helper'
import React from 'react'



export default function FormMemberPreviewer(props = {}) {
  const Helper = UI.Section.Form.Member
  const UseReactive = UI.React.hooks.useReactive
  let { reactive, member } = Helper.getProps(props)
  let type = UseReactive(reactive, { id: member.id, prop: 'type' })?.value
  if (!type) { return null }
  const Previewer = UI.try([
    `React.Section.Form.${Struct.string.capitalize(type)}.Previewer`,
    'React.Section.Form.Member.Previewer.Fallback',
  ])
  return (<Previewer {...props} />)
}
