import { useState, useEffect } from 'react'
import { UI } from 'rcfm/helper'



export default function useDebounceOnChange(options = {}) {
  let { value, parseValue, onChange, debounce } = options
  let [outerValue, setOuterValue] = useState(value)
  let [innerValue, setInnerValue] = useState(value)
  let [debouncer, setDebouncer] = useState(null)
  let decoratedOnChange = (newValue) => {
    parseValue && (newValue = parseValue(newValue))
    setInnerValue(newValue)
    debouncer ? debouncer.run(newValue) : onChange && onChange(newValue)
  }
  if (value !== outerValue) {
    setOuterValue(value)
    setInnerValue(value)
  }
  useEffect(() => {
    if (debouncer || !onChange || !debounce) { return }
    setDebouncer(UI.Debouncer.create({ runner: onChange, interval: debounce }))
    return () => {
      if (!debouncer) { return }
      debouncer.abort()
      setDebouncer(null)
    }
  }, [debouncer, onChange, debounce])
  return { value: innerValue, onChange: decoratedOnChange }
}
