import { UI } from 'rcfm/helper'
import React from 'react'



export default function FieldsetHeader(props = {}) {
  const GetProps = UI.Section.Form.Member.getProps
  const JoinErrors = UI.Section.Form.Member.joinErrors
  const UseReactive = UI.React.hooks.useReactive
  const Highlight = UI.use('React.Component.Highlight')
  const Actions = UI.use('React.Section.Form.Member.Previewer.Actions')
  const Header = UI.use('React.Section.Form.Fieldset.Header')
  let { reactive, member } = GetProps(props)
  let state = UseReactive(reactive, { id: member.id })
  return (<div className="rcfm-form-fieldset-previewer-header">
    <Actions {...{ ...props, action: ['edit'] }} />
    <Highlight
      type="error"
      on={state.errors ? true : false}
      hint={JoinErrors(state.errors)}
    >
      <Header reactive={reactive} member={member} />
    </Highlight>
  </div>)
}
