import { UI } from 'rcfm/helper'
import React from 'react'



export default function ValidationItemName(props = {}) {
  const Helper = UI.Section.Form.Member.PropEditor.Validation
  const SingleSelect = UI.use('React.Component.Form.SingleSelect')
  let { reactive, member, value, onChange } = props
  let options = Helper.getValidators({ reactive, member })
  return (
    <SingleSelect
      value={value.name} options={options}
      onChange={(name) => onChange({ action: 'update', name })}
    />
  )
}
