(() => {
  const originalRequire = window.require
  const fallbackRequire = (pkg) => ({
    'rcfm/helper': { UI: window.ReactiveForm.UI }, // [TODO]
    'react': window.React,
    'react-dom': window.ReactDOM,
    'prop-types': window.PropTypes,
  })[pkg]
  window.require = (pkg) => {
    if (originalRequire) {
      return originalRequire(pkg) || fallbackRequire(pkg)
    } else {
      return fallbackRequire(pkg)
    }
  }
})()
