import { UI } from 'rcfm/helper'
import React from 'react'



export default function WhenBranchRule(props = {}) {
  const Helper = UI.Section.Form.Member.PropEditor.Condition
  const NS = 'React.Section.Form.Member.PropEditor'
  const [Logic, Provider, ValidationItem, ButtonGroup, Button, Highlight] = [
    UI.use(`${NS}.Condition.WhenBranch.RuleLogic`),
    UI.use(`${NS}.Condition.WhenBranch.RuleProvider`),
    UI.use(`${NS}.Validation.Item`),
    UI.use('React.Component.ButtonGroup'),
    UI.use('React.Component.Button'),
    UI.use('React.Component.Highlight'),
  ]
  let { reactive, member, value, onChange, exception } = props
  let provider = reactive.schema.reference.parse(value.expression.value)
  let hint= exception?.message || ''
  let elsCls = 'rcfm-form-member-editor-condition-when-branch-rule-elements'
  return (<div className="rcfm-form-member-editor-condition-when-branch-rule">
    <Highlight type="error" hint={hint}
      on={
        Helper.isLogicException(exception) ||
        Helper.isProviderException(exception)
      } >
      <div className={elsCls}>
        {!value.logic ? null :
          <Logic
            reactive={reactive} member={member}
            value={value} onChange={onChange}
          />
        }
        <Provider
          reactive={reactive} member={member}
          value={value} onChange={onChange}
        />
        <ButtonGroup>
          <Button
            color="danger" icon="cross"
            onClick={() => onChange({ target: 'rule', action: 'delete' })}
          />
          <Button
            color="secondary" icon="plus"
            onClick={() => onChange({ target: 'rule', action: 'append' })}
          />
        </ButtonGroup>
      </div>
    </Highlight>
    {!value.expression.value ? null :
      <Highlight type="error"
        on={Helper.isValidationException(exception)} hint={hint} >
        <div className={elsCls}>
          <ValidationItem
            reactive={reactive} member={provider}
            value={value.expression} actions={['update']}
            onChange={(mu) => onChange({
              ...mu, target: 'rule', action: 'update',
            }) }
          />
        </div>
      </Highlight>
    }
  </div>)
}
