import { UI } from 'rcfm/helper'
import React from 'react'



export default function NumberInput(props = {}) {
  const GetProps = UI.Component.Form.NumberInput.getProps
  const UseNonblankName = UI.React.hooks.useNonblankName
  const UseDebounceOnChange = UI.React.hooks.useDebounceOnChange
  let { wrapperProps, inputProps } = GetProps(props)
  inputProps.readonly && (inputProps.disabled = true)
  inputProps.readOnly = inputProps.readonly
  delete inputProps.readonly
  let name = UseNonblankName(inputProps.name)
  let { value, onChange } = UseDebounceOnChange({
    value: inputProps.value,
    debounce: inputProps.debounce,
    onChange: inputProps.onChange,
    parseValue: (e) => e.target.value.length ? Number(e.target.value) : NaN,
  })
  if (isNaN(value)) { value = '' }
  Object.assign(inputProps, { name, value, onChange })
  return (
    <div {...wrapperProps} ><input {...inputProps} /></div>
  )
}
