import { UI } from 'rcfm/helper'
import React from 'react'



export default function Editor(prop = {}) {
  const Helper = UI.Section.Form.Member.PropEditor.Condition
  const [Highlight, WhenBranch, ElseBranch] = [
    UI.use('React.Component.Highlight'),
    UI.use('React.Section.Form.Member.PropEditor.Condition.WhenBranch'),
    UI.use('React.Section.Form.Member.PropEditor.Condition.ElseBranch'),
  ]
  let {
    reactive, member, exception, branches, ResultComponent, onChange,
  } = Helper.getProps(prop)
  let [hint, data] = [exception?.message || '', exception?.data || {}]
  return (<div className="rcfm-form-member-editor-condition">
    <Highlight type="error" on={Helper.isWhensException(exception)} hint={hint}>
      {branches.whens.map((branch, branchIndex) =>
        <WhenBranch
          key={branchIndex}
          reactive={reactive} member={member}
          exception={data.branchIndex === branchIndex ? exception : null}
          value={branch} ResultComponent={ResultComponent}
          onChange={(mu) => onChange({ ...mu, branchIndex })}
        />,
      )}
    </Highlight>
    <ElseBranch
      reactive={reactive} member={member}
      exception={data.branchIndex === branches.whens.length ? exception : null}
      value={branches.else} ResultComponent={ResultComponent}
      onChange={onChange}
    />
  </div>)
}
