import { I18n } from 'rcfm'
import { UI } from 'rcfm/helper'
import React from 'react'



export default function OptionItem(props = {}) {
  const [Button, ButtonGroup, TextInput, Highlight] = [
    UI.use('React.Component.Button'),
    UI.use('React.Component.ButtonGroup'),
    UI.use('React.Component.Form.TextInput'),
    UI.use('React.Component.Highlight'),
  ]
  let { value, exception, onChange } = props
  return (<div className="rcfm-form-member-editor-prop-options-item">
    <Highlight type="error"
      hint={exception?.message}
      on={exception ? true : false}
    >
      <div className="rcfm-form-member-editor-prop-options-item-elements">
        <label className="rcfm-fmlabel rcfm-fmtag">
          {I18n.global.t(
            'section.form.member.propEditor.options.each.labelLabel',
          )}
        </label>
        <TextInput
          value={value.label}
          onChange={(label) => onChange({ action: 'update', label })}
        />
        <label className="rcfm-fmlabel rcfm-fmtag">
          {I18n.global.t(
            'section.form.member.propEditor.options.each.valueLabel',
          )}
        </label>
        <TextInput
          value={value.value}
          onChange={(value) => onChange({ action: 'update', value })}
        />
        <ButtonGroup>
          <Button
            color="danger" icon="cross"
            onClick={() => onChange({ action: 'delete' })}
          />
          <Button
            color="secondary" icon="plus"
            onClick={() => onChange({ action: 'append' })}
          />
        </ButtonGroup>
      </div>
    </Highlight>
  </div>)
}
