import { Struct } from 'rcfm'
import { UI } from 'rcfm/helper'
import React from 'react'



export default function FormMemberEditor(props = {}) {
  const Helper = UI.Section.Form.Member
  const UseReactive = UI.React.hooks.useReactive
  let { reactive, member, onAction } = Helper.getProps(props)
  let type = UseReactive(reactive, { id: member.id, prop: 'type' })?.value
  if (!type) { return null }
  const Editor = UI.try([
    `React.Section.Form.${Struct.string.capitalize(type)}.Editor`,
    'React.Section.Form.Member.Editor.Fallback',
  ])
  return (<Editor reactive={reactive} member={member} onAction={onAction} />)
}
