import { UI } from 'rcfm/helper'
import React from 'react'



export default function ValidationEditor(props = {}) {
  const Helper = UI.Section.Form.Member.PropEditor.Validation
  const [Highlight, ValidationItem] = [
    UI.use('React.Component.Highlight'),
    UI.use('React.Section.Form.Member.PropEditor.Validation.Item'),
  ]
  let {
    reactive, member, value, exception, actions, onChange,
  } = Helper.getProps(props)
  return (<div className="rcfm-form-member-editor-prop-validation">
    {value.map((v, index) =>
      <Highlight type="error"
        key={index} hint={exception?.message}
        on={exception?.data?.validationIndex === index}
      >
        <ValidationItem
          reactive={reactive} member={member} value={v} actions={actions}
          onChange={(mutation) => onChange({ ...mutation, index })}
        />
      </Highlight>,
    )}
  </div>)
}
