import { UI } from 'rcfm/helper'
import React from 'react'



export default function FallbackEditor(props = {}) {
  const Helper = UI.Section.Form.Member
  const PropEditor = UI.use('React.Section.Form.Member.PropEditor')
  let { reactive, member, onAction } = Helper.getProps(props)
  let schema = reactive.schema.for({ id: member.id })
  return (<div className="rcfm-form-member-editor-fallback">
    {schema.map((S) =>
      <PropEditor
        key={S.prop}
        reactive={reactive}
        onAction={onAction}
        member={{ id: member.id, prop: S.prop }}
      />,
    )}
  </div>)
}
