import { Struct } from 'rcfm'
import { UI } from 'rcfm/helper'
import React from 'react'



export default function Editor(props = {}) {
  const Helper = UI.Section.Form.Member.PropEditor
  const [Tabs, TabPane, Condition] = [
    UI.use('React.Component.Tabs'),
    UI.use('React.Component.TabPane'),
    UI.use('React.Section.Form.Member.PropEditor.Condition'),
  ]
  const UseReactive = UI.React.hooks.useReactive
  let { reactive, member } = Helper.getProps(props)
  let PropCap = Struct.string.capitalize(member.prop)
  const PropEditor = UI.try([
    `React.Section.Form.Member.PropEditor.${PropCap}`,
    'React.Section.Form.Member.PropEditor.Simple',
  ])
  let {
    title, labels, activeIndex, onSwitch, onChange, defaultResult,
  } = Helper.getTabsProps(props)
  let state = UseReactive(reactive, member)
  return (
    <Tabs
      title={title} labels={labels}
      activeIndex={activeIndex} onSwitch={onSwitch}
    >
      <TabPane>
        {activeIndex !== 0 ? null : <PropEditor
          reactive={reactive} member={member}
          onChange={onChange} value={state.raw.value}
          exception={state.error ? state.error[0] : null}
        />}
      </TabPane>
      <TabPane>
        {activeIndex !== 1 ? null : <Condition
          reactive={reactive} member={member}
          onChange={onChange} value={state.raw.value}
          exception={state.error ? state.error[0] : null}
          defaultResult={defaultResult} ResultComponent={PropEditor}
        />}
      </TabPane>
    </Tabs>
  )
}
