import { UI } from 'rcfm/helper'
import React from 'react'



export default function Button(props = {}) {
  const GetProps = UI.Component.Button.getProps
  const Icon = UI.use('React.Component.Icon')
  let { buttonProps, iconProps } = GetProps(props)
  return (
    <button {...buttonProps}>
      {(() => {
        if (!iconProps.name) { return buttonProps.children }
        if (iconProps.position === 'middle') {
          return <Icon name={iconProps.name}></Icon>
        }
        return (<>
          <Icon name={iconProps.name}></Icon>
          {buttonProps.children}
        </>)
      })()}
    </button>
  )
}
