import { UI } from 'rcfm/helper'
import React from 'react'



export default function FallbackPreviewer(props = {}) {
  const Helper = UI.Section.Form.Member
  const UseReactive = UI.React.hooks.useReactive
  const Highlight = UI.use('React.Component.Highlight')
  const MemberRenderer = UI.use('React.Section.Form.Member')
  const Actions = UI.use('React.Section.Form.Member.Previewer.Actions')
  let { reactive, member, onAction } = Helper.getProps(props)
  let state = UseReactive(reactive, { id: member.id })
  let highlight = props.highlight === true || props.highlight === member.id
  return (<div className={UI.joinClass([
    'rcfm-form-member-previewer-fallback',
    highlight ? 'rcfm-form-member-previewer-fallback-highlight' : '',
  ])}>
    <Actions {...props} />
    <Highlight type="error"
      on={state?.errors ? true : false}
      hint={Helper.joinErrors(state?.errors)}
    >
      <fieldset disabled
        className="rcfm-form-member-previewer-fallback-member-wrapper">
        <MemberRenderer
          reactive={reactive} member={member} onAction={onAction}
        />
      </fieldset>
    </Highlight>
  </div>)
}
