import { Struct } from 'rcfm'
import { UI } from 'rcfm/helper'
import React from 'react'



export default function Editor(props = {}) {
  const Helper = UI.Section.Form.Member.PropEditor
  const UseReactive = UI.React.hooks.useReactive
  let { reactive, member } = Helper.getProps(props)
  let { onAction } = UI.Section.Form.Member.getProps(props)
  let type = UseReactive(reactive, { id: member.id, prop: 'type' })?.value
  if (!type) { return null }
  const PropEditor = UI.try([
    `React.Section.Form.${Struct.string.capitalize(type)}.PropEditor`,
    'React.Section.Form.Member.PropEditor.Fallback',
  ])
  return (
    <PropEditor
      reactive={reactive} member={member} onAction={onAction}
    />
  )
}
