import { I18n } from 'rcfm'
import { UI } from 'rcfm/helper'
import React from 'react'



export default function WhenBranch(props = {}) {
  const Helper = UI.Section.Form.Member.PropEditor.Condition
  const [Button, ButtonGroup, Highlight, BranchRule] = [
    UI.use('React.Component.Button'),
    UI.use('React.Component.ButtonGroup'),
    UI.use('React.Component.Highlight'),
    UI.use('React.Section.Form.Member.PropEditor.Condition.WhenBranch.Rule'),
  ]
  let { reactive, member, value, onChange, ResultComponent, exception } = props
  let [hint, data] = [exception?.message || '', exception?.data || {}]
  return (<div className="rcfm-form-member-editor-condition-when-branch">
    <h6 className="rcfm-form-member-editor-condition-branch-title">
      {I18n.global.t(
        'section.form.member.propEditor.condition.whenBranch.title',
      )}
    </h6>
    <Highlight type="error" on={Helper.isRulesException(exception)} hint={hint}>
      {value.rules.map((rule, ruleIndex) =>
        <BranchRule
          key={ruleIndex}
          reactive={reactive} member={member}
          exception={data.ruleIndex === ruleIndex ? exception : null}
          value={rule} onChange={(mu) => onChange({ ...mu, ruleIndex })}
        />,
      )}
    </Highlight>
    <h6 className="rcfm-form-member-editor-condition-branch-title">
      {I18n.global.t(
        'section.form.member.propEditor.condition.whenBranch.resultTitle',
      )}
    </h6>
    <ResultComponent
      reactive={reactive} member={member} value={value.result}
      exception={Helper.isntConditionException(exception) ? exception : null}
      onChange={(result) => onChange({
        target: 'when', action: 'update', result,
      })}
    />
    <ButtonGroup fluid>
      <Button
        color="danger"
        onClick={() => onChange({ target: 'when', action: 'delete' })}
      >
        {I18n.global.t(
          'section.form.member.propEditor.condition.whenBranch.delete',
        )}
      </Button>
      <Button
        color="secondary"
        onClick={() => onChange({ target: 'when', action: 'append' })}
      >
        {I18n.global.t(
          'section.form.member.propEditor.condition.whenBranch.add',
        )}
      </Button>
    </ButtonGroup>
  </div>)
}

