import { UI } from 'rcfm/helper'
import React from 'react'



export default function NumberField(props = {}) {
  const GetProps = UI.Section.Form.Member.getProps
  const Required = UI.Section.Form.Field.required
  const Readonly = UI.Section.Form.Field.readonly
  const UseReactive = UI.React.hooks.useReactive
  const Field = UI.use('React.Component.Form.Field')
  const NumberInput = UI.use('React.Component.Form.NumberInput')
  let { reactive, member, onAction } = GetProps(props)
  let id = member.id
  let { values, errors } = UseReactive(reactive, { id })
  return (
    <Field
      label={values.label}
      hint={values.hint}
      required={Required(reactive, member)}
      errors={errors && Object.values(errors)}
    >
      <NumberInput
        name={values.name}
        value={values.value}
        placeholder={values.placeholder}
        invalid={errors ? true : false}
        readonly={Readonly(reactive, member)}
        onChange={(value) => onAction({
          action: 'update', reactive,
          id: member.id, prop: 'value', value,
        })}
      />
    </Field>
  )
}
