import { UI } from 'rcfm/helper'
import React from 'react'



export default function FieldsetPreviewer(props = {}) {
  const GetProps = UI.Section.Form.Member.getProps
  const UseReactive = UI.React.hooks.useReactive
  const MemberPreviewer = UI.use('React.Section.Form.Member.Previewer')
  const Header = UI.use('React.Section.Form.Fieldset.Previewer.Header')
  const ChdPicker = UI.use('React.Section.Form.Fieldset.Previewer.ChildPicker')
  let { reactive, member, onAction } = GetProps(props)
  let id = member.id
  let children = UseReactive(reactive, { id, prop: 'children' }).value
  let addChild = (type, order) => onAction({
    action: 'add', reactive, parentId: id, props: { type, order },
  })
  let highlight = props.highlight === true || props.highlight === id
  return (<div className={UI.joinClass([
    'rcfm-form-fieldset-previewer',
    highlight ? 'rcfm-form-fieldset-previewer-highlight' : '',
  ])}>
    <Header {...props} />
    <main className="rcfm-form-fieldset-previewer-children">
      {children.length ? children.map((c, index) =>
        <div key={c.id} className="rcfm-form-fieldset-previewer-child">
          <MemberPreviewer
            reactive={reactive} member={c} onAction={onAction}
            highlight={props.highlight}
          />
          <ChdPicker onPick={(type) => addChild(type, index + 1)} />
        </div>,
      ) : <ChdPicker onPick={(type) => addChild(type, 0)} />}
    </main>
  </div>)
}
