import { UI } from 'rcfm/helper'
import React, { useState } from 'react'



export default function SingleSelect(props = {}) {
  const GetProps = UI.Component.Form.SingleSelect.getProps
  const FindLabel = UI.Component.Form.SingleSelect.findLabel
  const UseNonblankName = UI.React.hooks.useNonblankName
  let selectProps = GetProps(props)
  let { inputProps, wrapperProps, includeblank, value, options } = selectProps
  inputProps.readonly && (inputProps.disabled = true)
  inputProps.readOnly = inputProps.readonly
  delete inputProps.readonly
  let { disabled, readOnly, onChange, placeholder } = inputProps
  let initLabel = FindLabel(value, options)
  let [label, setLabel] = useState(initLabel)
  let [innerValue, setInnerValue] = useState(value)
  onChange && label !== initLabel && setLabel(initLabel)
  onChange && value !== innerValue && setInnerValue(value)
  let name = UseNonblankName(inputProps.name)
  let onChangeHandler = (e) => {
    let selected = options[e.target.selectedIndex - 1]
    setLabel(label = selected ? selected.label : '')
    setInnerValue(value = selected?.value)
    onChange && onChange(value)
  }
  Object.assign(inputProps, {
    name, value: innerValue, onChange: onChangeHandler,
  })
  return (<div {...wrapperProps} >
    <select {...inputProps} >
      <option className="rcfm-fmtag"
        style={{ display: includeblank ? 'initial' : 'none' }}></option>
      {options.map((o) =>
        <option className="rcfm-fmtag" key={o.value} value={o.value}>
          {o.label}
        </option>,
      )}
    </select>
    <input className="rcfm-fmtag"
      tabIndex={-1} value={label} onChange={() => {}}
      placeholder={placeholder} disabled={disabled} readOnly={readOnly}
    />
  </div>)
}

