import { Struct } from 'rcfm'
import { UI } from 'rcfm/helper'
import React from 'react'



export default function FormMember(props = {}) {
  const Helper = UI.Section.Form.Member
  const UseReactive = UI.React.hooks.useReactive
  let { reactive, member, onAction } = Helper.getProps(props)
  let type = UseReactive(reactive, { id: member.id, prop: 'type' })?.value
  if (!type) { return null }
  const Member = UI.use(`React.Section.Form.${Struct.string.capitalize(type)}`)
  return (
    <Member reactive={reactive} member={member} onAction={onAction} />
  )
}
