import { UI } from 'rcfm/helper'
import React from 'react'



export default function ValidationItem(props = {}) {
  const [Button, ButtonGroup, NameEditor, RuleEditor] = [
    UI.use('React.Component.Button'),
    UI.use('React.Component.ButtonGroup'),
    UI.use('React.Section.Form.Member.PropEditor.Validation.ItemName'),
    UI.use('React.Section.Form.Member.PropEditor.Validation.ItemRule'),
  ]
  let { reactive, member, value, onChange, actions } = props
  return (<div className="rcfm-form-member-editor-prop-validation-item">
    <NameEditor
      reactive={reactive} member={member}
      value={value} onChange={onChange}
    />
    {!value.name ? null :
      <RuleEditor
        reactive={reactive} member={member}
        value={value} onChange={onChange}
      />
    }
    {!(actions.includes('delete') || actions.includes('append')) ? null :
      <ButtonGroup>
        {!actions.includes('delete') ? null :
          <Button
            color="danger" icon="cross"
            onClick={() => onChange({ action: 'delete' })}
          />
        }
        {!actions.includes('append') ? null :
          <Button
            color="secondary" icon="plus"
            onClick={() => onChange({ action: 'append' })}
          />
        }
      </ButtonGroup>
    }
  </div>)
}
