import { UI } from 'rcfm/helper'
import React from 'react'



function PannelItem(props = {}) {
  let { type } = props
  const Item = UI.try([
    `React.Section.Form.Fieldset.Previewer.ChildPicker.Pannel.Item.${type}`,
    'React.Section.Form.Fieldset.Previewer.ChildPicker.Pannel.Item.Fallback',
  ])
  return (<Item {...props} />)
}

function PannelItemFallback(props = {}) {
  let { type, icon, title, onClick } = props
  const Button = UI.use('React.Component.Button')
  return (
    <Button
      fluid color="secondary" leftIcon={icon}
      onClick={() => onClick(type)}
    >
      {title}
    </Button>
  )
}

function Pannel(props = {}){
  const Helper = UI.Section.Form.Fieldset
  const [ButtonGroup, PannelItem] = [
    UI.use('React.Component.ButtonGroup'),
    UI.use('React.Section.Form.Fieldset.Previewer.ChildPicker.Pannel.Item'),
  ]
  let groups = Helper.getChildGroups()
  return (<div className="rcfm-form-fieldset-previewer-child-picker-pannel">
    {groups.map((group) => <div key={group.name}>
      <h6 className="rcfm-form-fieldset-previewer-child-picker-pannel-title">
        {group.title}
      </h6>
      <ButtonGroup vertical fluid>
        {group.types.map((type) =>
          <PannelItem
            key={type.type} type={type.type} icon={type.icon}
            title={type.title} onClick={() => props.onPick(type.type)}
          />,
        )}
      </ButtonGroup>
    </div>)}
  </div>)
}



export {
  Pannel,
  Pannel as default,
  PannelItem,
  PannelItemFallback,
}
