import { UI } from 'rcfm/helper'
import React from 'react'



export default function MultipleSelect(props = {}) {
  const GetProps = UI.Component.Form.MultipleSelect.getProps
  const UseNonblankName = UI.React.hooks.useNonblankName
  let { inputProps, wrapperProps, options, value } = GetProps(props)
  inputProps.readonly && (inputProps.disabled = true)
  inputProps.readOnly = inputProps.readonly
  delete inputProps.readonly
  let onChange = inputProps.onChange
  let name = UseNonblankName(inputProps.name)
  let onChangeHandler = !onChange ? null : (e) => {
    let [newVal, opts] = [[], Array.from(e.target.options)]
    opts.forEach((o, index) => o.selected && newVal.push(options[index].value))
    onChange(newVal)
  }
  Object.assign(inputProps, {
    name, onChange: onChangeHandler,
    [onChangeHandler ? 'value' : 'defaultValue']: value,
  })
  return (<div {...wrapperProps} >
    <select {...inputProps} >
      {options.map((o) =>
        <option className="rcfm-fmtag" key={o.value} value={o.value}>
          {o.label}
        </option>,
      )}
    </select>
  </div>)
}
