import { UI } from 'rcfm/helper'
import React from 'react'



export default function Header(props = {}) {
  const GetProps = UI.Section.Form.Member.getProps
  const UseReactive = UI.React.hooks.useReactive
  let { reactive, member } = GetProps(props)
  let state = UseReactive(reactive, { id: member.id })
  let [title, description] = [state.values.label, state.values.description]
  if (!title && !description) { return null }
  return (<div className="rcfm-form-fieldset-header">
    {
      title &&
      <h2 className="rcfm-form-fieldset-header-title">{title}</h2>
    }
    {
      description &&
      <p className="rcfm-form-fieldset-header-description">{description}</p>
    }
  </div>)
}
