import { UI } from 'rcfm/helper'
import React, { useState } from 'react'



export default function WhenBranchRuleProvider(props = {}) {
  const Help = UI.Section.Form.Member.PropEditor.Condition
  const useReactive = UI.React.hooks.useReactive
  const SingleSelect = UI.use('React.Component.Form.SingleSelect')
  let { reactive, member, value, onChange } = props
  let [options, setOptions] = useState(Help.getProviders({ reactive, member }))
  let refershOptions = () => setOptions(Help.getProviders({ reactive, member }))
  let provider = reactive.schema.reference.parse(value.expression.value)
  let providerLabel = useReactive(reactive, provider)?.value
  return (
    <SingleSelect
      className="rcfm-form-member-editor-condition-when-branch-rule-provider"
      key={providerLabel} options={options}
      value={Help.joinProvider(provider)} onFocus={refershOptions}
      onChange={(val) => refershOptions(onChange({
        target: 'rule', action: 'update',
        value: reactive.schema.reference.stringify(Help.splitProvider(val), {
          format: 'interpolation',
        }),
      }))}
    />
  )
}
