import { UI } from 'rcfm/helper'
import React from 'react'



export default function WhenBranchRuleLogic(props = {}) {
  const Helper = UI.Section.Form.Member.PropEditor.Condition
  const SingleSelect = UI.use('React.Component.Form.SingleSelect')
  let { value, onChange } = props
  let logics = Helper.getLogics()
  return (
    <SingleSelect
      className="rcfm-form-member-editor-condition-when-branch-rule-logic"
      options={logics} value={value.logic}
      onChange={(logic) => onChange({
        target: 'rule', action: 'update', logic,
      })}
    />
  )
}
